@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&display=swap);
* {
    box-sizing: border-box;
}

body {
    background-color: #46529d;
    font-family: 'Balsamiq Sans', cursive;
    color: #fff;
    margin: 5rem auto;
}

.container {
    width: 60%;
    margin: 0 auto;
}

.list-tasks {
    border: 1px solid #00BCD4;
    box-shadow: 0px 0px 5px 3px rgba(0, 163, 212, 1);
}

@media (max-width: 992px) {
    .container {
        width: 80%;
    }
}

.text-center {
    text-align: center;
}
.to-do-list-tasks {
    background-color: #fff;
}

.to-do-list-tasks .no-tasks {
    background-color: #fff;
    color: #46529d;
    padding: 1rem 0.4rem;
}

.to-do-list-tasks .t-header {
    display: flex;
    background-color: #30B9F0;
}

.to-do-list-tasks .t-header .title {
    color: #fff;
    padding: 1rem 0.4rem;
    font-weight: bolder;
    font-size: 20px;
}

.to-do-list-tasks .t-header .task,
.to-do-list-tasks>div:not(.t-header) .task {
    flex-basis: 60%;
    padding: 1rem 0.4rem;
}

.to-do-list-tasks .t-header .priority,
.to-do-list-tasks>div:not(.t-header) .priority {
    flex-basis: 30%;
    padding: 1rem 0.4rem;
}

.to-do-list-tasks .t-header .times,
.to-do-list-tasks>div:not(.t-header) .times {
    flex-basis: 10%;
    padding: 1rem 0.4rem;
    color: #30B9F0;
    cursor: pointer;
}

.to-do-list-tasks>div>span {
    color: #264653;
}

.to-do-list-tasks>div:not(.t-header) {
    display: flex;
}
.to-do-list-add form {
    display: flex;
}

@media (max-width: 992px) {
    .to-do-list-add form {
        display: block;
    }
    .to-do-list-add form input[type="submit"] {
        width: 100%;
    }
}

.to-do-list-add form .task {
    flex-basis: 60%;
}

.to-do-list-add form .task input {
    width: 100%;
    background-color: #eee;
}

.to-do-list-add form .priority {
    flex-basis: 30%;
}

.to-do-list-add form .priority input {
    width: 100%;
    background-color: #eee;
}

.to-do-list-add form input {
    flex-basis: 10%;
}

.to-do-list-add form input {
    border: none;
    padding: 1rem;
    outline: none;
    color: #30B9F0;
    font-weight: bold;
}

.to-do-list-add form input[type="submit"] {
    cursor: pointer;
    color: #fff;
    background-color: #30B9F0;
}
